import React from "react";
import { Link } from "react-router-dom";

const Layout = (props) => {

    const headerButtonMap = props.headerButtons;

    const headerButtons =  headerButtonMap.map((headerButton) => {
        return
            <li class="nav-item">
            <a class="nav-link active" aria-current="page" href={`${headerButton.href}`}>
                {headerButton.buttonName}
            </a>
            </li>
    });

  return (
    <React.Fragment>
      <nav class="navbar navbar-expand-lg navbar-light bg-light">
        <div class="container-fluid">
          <a class="navbar-brand" href="#">
            KiranAddagarla
          </a>
          <button
            class="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span class="navbar-toggler-icon"></span>
          </button>
          <div
            class="justify-content-end collapse navbar-collapse"
            id="navbarSupportedContent"
          >
            <ul class="navbar-nav me-auto mb-2 mb-lg-0">
              {headerButtons}
            </ul>
          </div>
        </div>
      </nav>
    </React.Fragment>
  );
};

export default Layout;
